import { BrowserRouter as Router, Routes, Route, useNavigate } from "react-router-dom";
import logo from './assets/ccpp-logo.svg';  //    
import './App.css';
//   `src/`    .
import Explore from './Explore';
import Jobs from './Jobs';
import StackBooks from './StackBooks';
import BookDetail from './BookDetail';

function Home() {
	const navigate = useNavigate(); //    hook

	//    (   )
	const announcements = [
		{ id: 1, title: "  ", company: " ", deadline: "2025.12.31", isNew: true },
		{ id: 2, title: " ", company: "", deadline: "2025.12.15", isNew: true },
	];

	return (
		<div className="App">
			<header className="header">
				<div className="logo">
					{/*     */}
					<img src={logo} alt="CCPP Logo" className="logo-image" />
				</div>
				<div className="search-container">
					<input type="text" placeholder=" /  " className="search-input" />
				</div>
			</header>

			<main className="main-content">
				<div className="content-layout">
					<div className="announcement-list">
						<h2> </h2>
						<div className="announcement-items">
							{announcements.map(item => (
								<div key={item.id} className="announcement-item">
									<div className="announcement-header">
										<h3>{item.title}</h3>
										{item.isNew && <span className="new-badge">New</span>}
									</div>
									<div className="announcement-info">
										<span>{item.company}</span>
										<span className="deadline">: {item.deadline}</span>
									</div>
								</div>
							))}
						</div>
						<button className="btn more-btn" onClick={() => navigate('/jobs')}></button>
					</div>

					<div className="explore-section">
						<div className="carousel">
							<div className="carousel-items">
								<img src="/images/book1.jpg" alt="Book 1" />
								<img src="/images/book2.jpg" alt="Book 2" />
								<img src="/images/book3.jpg" alt="Book 3" />
							</div>
							<div className="carousel-dots">
								<span className="dot active"></span>
								<span className="dot"></span>
								<span className="dot"></span>
								<span className="dot"></span>
							</div>
						</div>

						{/*      /explore   */}
						<button className="btn explore-btn" onClick={() => navigate("/explore")}>
							
						</button>
					</div>
				</div>
			</main>
		</div>
	);
}

// Explore page moved to src/pages/Explore.js

function App() {
	return (
		<Router>
			<Routes>
				<Route path="/" element={<Home />} />
	<Route path="/explore" element={<Explore />} />
			<Route path="/jobs" element={<Jobs />} />
			<Route path="/stack/:stackName" element={<StackBooks />} />
			<Route path="/book/:id" element={<BookDetail />} />
			</Routes>
		</Router>
	);
}

export default App;
