// Centralized book data and helpers
const BOOKS = [
  { id: 1, title: 'Practical Statistics for Data Scientists', author: 'Peter Bruce', price: '25,000', description: '       .' },
  { id: 2, title: 'Deep Learning with Python', author: "Franois Chollet", price: '30,000', description: '     .' },
  { id: 3, title: 'Designing Data-Intensive Applications', author: 'Martin Kleppmann', price: '35,000', description: '      .' },
  { id: 4, title: 'Clean Code', author: 'Robert C. Martin', price: '22,000', description: '      .' },
  { id: 5, title: "You Don't Know JS", author: 'Kyle Simpson', price: '18,000', description: '     .' },

  // Python stack
  { id: 101, title: 'Python Crash Course', author: 'Eric Matthes', price: '27,000', description: '   .' },
  { id: 102, title: 'Fluent Python', author: 'Luciano Ramalho', price: '45,000', description: '     .' },

  // React stack
  { id: 201, title: 'Learning React', author: 'Alex Banks', price: '28,000', description: '  Hooks .' },
  { id: 202, title: 'Fullstack React', author: 'Fullstack Team', price: '40,000', description: '   .' },
  { id: 203, title: "You Don't Know JS", author: 'Kyle Simpson', price: '18,000', description: '  .' },

  // Kafka / Data infra
  { id: 301, title: 'Kafka: The Definitive Guide', author: 'Neha Narkhede', price: '50,000', description: '   .' },
  { id: 302, title: 'Designing Data-Intensive Applications', author: 'Martin Kleppmann', price: '35,000', description: '    .' },
];

const STACK_MAP = {
  Python: [101, 102, 1],
  React: [201, 202, 203, 5],
  Kafka: [301, 302, 3],
};

export function getBooks() { return BOOKS; }
export function getBookById(id) { return BOOKS.find(b => Number(b.id) === Number(id)); }
export function getBooksByStack(stackName) {
  const ids = STACK_MAP[stackName] || [];
  return ids.map(id => getBookById(id)).filter(Boolean);
}

export default { getBooks, getBookById, getBooksByStack };
