import React from 'react';
import { useNavigate } from 'react-router-dom';
import './Jobs.css';

const ANNOUNCEMENTS = [
  { id: 1, title: '  ', company: ' ', deadline: '2025.12.31', isNew: true, location: '' },
  { id: 2, title: ' ', company: '', deadline: '2025.12.15', isNew: true, location: '' },
  { id: 3, title: '  (Spring)', company: '', deadline: '2026.01.10', isNew: false, location: '' },
];

export default function Jobs() {
  const navigate = useNavigate();

  return (
    <div className="jobs-root">
      <header className="jobs-header">
        <button className="btn back-btn" onClick={() => navigate('/')}></button>
        <h1> </h1>
      </header>

      <main className="jobs-main">
        <div className="jobs-list">
          {ANNOUNCEMENTS.map(a => (
            <article key={a.id} className="job-card">
              <div className="job-header">
                <h3>{a.title}</h3>
                {a.isNew && <span className="new-badge">New</span>}
              </div>
              <div className="job-meta">{a.company}  {a.location}  : {a.deadline}</div>
              <p className="job-excerpt">   .    .</p>
              <div className="job-actions">
                <button className="btn"> </button>
              </div>
            </article>
          ))}
        </div>
      </main>
    </div>
  );
}
